import React from 'react';
import './App.css';
import ChickenCoop from "./component/coop/ChickenCoop"

import logo from './logo.png'

import {
    HashRouter as Router,
    Route,
    Switch,
    Link
} from "react-router-dom";
import CartButton from "./component/cart/CartButton";
import Cart from "./component/cart/Cart";
import About from "./component/about/About";
import Barn from "./component/barn/Barn";
import Contact from "./component/contact/Contact";
import Critters from "./component/critters/Critters";
import Chicken from "./component/coop/Chicken";

function App() {
  return (
      <Router basename="/">
          <div className="home">
              <header>
                  <img className="logo" src={logo} alt="logo"/>

                  <nav>
                      <ul>
                          <li>
                              <Link to="/">About</Link>
                          </li>
                          <li>
                              <Link to="/coop">Coop</Link>
                          </li>
                          <li>
                              <Link to="/barn">Barn</Link>
                          </li>
                          <li>
                              <Link to="/critters">Other Critters</Link>
                          </li>
                          <li>
                              <Link to="/contact">Contact</Link>
                          </li>
                          <li>
                              <CartButton/>
                          </li>
                      </ul>
                  </nav>
              </header>

              <Switch>
                  <Route exact path="/" component={About}/>
                  <Route exact path="/coop" component={ChickenCoop} />
                  <Route path="/coop/:name" render={(props) => <Chicken name={props.match.params.name} />} />
                  <Route path="/barn" component={Barn} />
                  <Route path="/critters" component={Critters} />
                  <Route path="/contact" component={Contact}/>
                  <Route path="/cart" component={Cart} />
              </Switch>

              <footer>Â© 2020, Lorum Ipsum Farms</footer>
          </div>
      </Router>
    // <div className="App">
    // </div>
  );
}

export default App;
