import React, {useEffect, useState} from "react";
import { useDispatch } from 'react-redux'
import {addToCartAction} from "../../actions";
import PropTypes from 'prop-types'
import './chicken.css'
import {Link} from "react-router-dom";

const Chicken = ({ name }) => {
    const dispatch = useDispatch();
    const [chickenInfo, setChickenInfo] = useState({ description: "", eggPrice: 0.0})
    const [isLoaded, setLoaded] = useState(false)
    const [error, setError] = useState()

    console.log(chickenInfo)

    useEffect(() => {
        // fetch("http://localhost:8001/api/chickens/" + name)
        // .then(res => res.json())
        // .then(
        //     (result) => {
        //         setLoaded(true)
        //         setChickenInfo(result)
        //     },
        //     (error) => {
        //         setLoaded(true)
        //         setError(error)
        //     }
        // )
        setChickenInfo(require('./Chickens.json').find(chicken => chicken.name === name))
        setLoaded(true)
    }, [])

    if (error) {
        return <div>Error: {error.message}</div>
    } else if (!isLoaded) {
        return <div>Loading...</div>
    } else {
        return (
            // <h1>chicken</h1>
            <div className="chicken">
                <h1>{name}</h1>
                <img className="photo" src={require('./images/' + name + '.jpg')} alt="chicken"/>
                <img className="photo" src={require('./images/' + name + ' Egg.jpg')} alt="egg"/>
                <p>{chickenInfo.description}</p>
                <p>${chickenInfo.eggPrice}</p>
                <button onClick={() => dispatch(addToCartAction(chickenInfo))}>Add to cart</button>
            </div>
        )
    }
}

Chicken.propTypes = {
    name: PropTypes.string.isRequired
}

export default Chicken