import Chicken from "./Chicken";
import React, {useEffect, useState} from "react";
import PropTypes from 'prop-types'

// import data from './Chickens.json'
import {Link} from "react-router-dom";

function ChickenCoop() {
    const [chickens, setChickens] = useState()
    const [isLoaded, setLoaded] = useState(false)
    const [error, setError] = useState()

    useEffect(() => {
        //  to enable node backend uncomment this
        // fetch("http://localhost:8001/api/chickens")
        //     .then(res => res.json())
        //     .then(
        //         (result) => {
        //             setLoaded(true)
        //             setChickens(result)
        //         },
        //         (error) => {
        //             setLoaded(true)
        //             setError(error)
        //         }
        //     )
        setChickens(require('./Chickens.json'))
        setLoaded(true)
    }, []);

    // console.log(chickens)

    if (error) {
        return <div>Error: {error.message}</div>
    } else if (!isLoaded) {
        return <div>Loading...</div>
    } else {
        return (
            <div className="chickencoop">
                {/*{chickens.map(chicken => (*/}
                {/*    <Chicken key={chicken.id} {...chicken} />*/}
                {/*))}*/}
                {chickens.map(chicken => (
                    <Link key={chicken.id} to={ 'coop/' + chicken.name}>
                        <img className="photo" src={require('./images/' + chicken.name + '.jpg')} alt="chicken"/>
                    </Link>


                    // <Link key={chicken.id} to={"coop/" + chicken.name.toLowerCase()}>
                    //     {/*<h1>{chicken.name}</h1>*/}
                    //     <img className="photo" src={require('./images/' + chicken.name + '.jpg')} alt="chicken"/>
                    // </Link>
                ))}
            </div>
        )
    }
}

ChickenCoop.propTypes = {
    isLoaded: PropTypes.bool,
    chickens: PropTypes.arrayOf(
        PropTypes.shape({
            id: PropTypes.number.isRequired,
            name: PropTypes.string.isRequired,
            // photo: PropTypes.func.isRequired,
            // description: PropTypes.string.isRequired,
            // eggPrice: PropTypes.number.isRequired
        }).isRequired
    ).isRequired,
    error: PropTypes.object
}

ChickenCoop.defaultProps = {
    isLoaded: false,
    chickens: require('./Chickens.json'),
    error: {}
}

export default ChickenCoop