

const cart = (state = [], action) => {
    console.log(action)

    switch (action.type) {
        case 'ADD_TO_CART':
            return {
                ...state,
                cart: addChickenToCart(state.cart, action.chicken)
            }
        default:
            return state
    }
}

const addChickenToCart = (cart = [], chicken) => {
    return [
        ...cart,
        chicken
    ]
}


export default cart