import {useSelector} from "react-redux";
import Chicken from "../coop/Chicken";
import React from "react";

function Cart() {
    const cart = useSelector(state => state.cart) || []

    console.log(cart)

    return (
        <div className="cart">
            {
                Array.from(groupBy(cart, cart => cart.name))
                    .map( ([key, value]) => (
                        <p>{key} - x {value.length}</p>
                    ))
            }

            <p>Total price: ${cart
                .map(chicken => chicken.eggPrice)
                .reduce((a, b) => a + b, 0)}</p>
        </div>
    )

}

function groupBy(list, keyGetter) {
    const map = new Map();
    list.forEach((item) => {
        const key = keyGetter(item);
        const collection = map.get(key);
        if (!collection) {
            map.set(key, [item]);
        } else {
            collection.push(item);
        }
    });
    return map;
}

export default Cart